.topbanner {
  position: relative;
  width: 890px;
  height: 410px;
  text-align: center;
  overflow: hidden; }
  .topbanner .imglist li, .topbanner .imglist li img {
    width: 890px;
    height: 410px; }
  .topbanner .imglist {
    width: 10000px; }
    .topbanner .imglist li {
      float: left; }
  .topbanner .dotlist {
    display: inline-block;
    position: relative;
    bottom: 30px; }
    .topbanner .dotlist li {
      float: left;
      width: 12px;
      height: 12px;
      border-radius: 50%;
      margin-left: 10px;
      margin-right: 10px; }
      .topbanner .dotlist li.active {
        background: #c6f; }
